@import url('https://fonts.googleapis.com/css2?family=Archivo&family=Archivo+Black&family=Gruppo&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 100vh;
    /* background: #0f6be2; */
    overflow-x: hidden;
}

a {
    text-decoration: none;
}

ul,
li {
    list-style: none;
}

/* 버튼 */
button,
input[type='button'] {
    border: none;
    outline: none;
    background: transparent;
    cursor: pointer;
}
.btn {
    min-width: 90px;
    font-size: 15px;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    border: 1px solid transparent;
    background: #000;
    border-radius: 12.5px;
    transition: background 0.5s border 0.5s;
}
.btn:hover {
    color: #000;
    background: transparent;
    border: 1px solid #000;
}

/* 헤더 */
header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    text-transform: uppercase;
    background: transparent;
    padding: 0 30px;
}

header nav {
    flex-basis: 150px;
    font-family: 'Gruppo', sans-serif;
    font-weight: bold;
    margin-right: auto;
}
nav ul {
    display: flex;
    gap: 10px;
}
nav ul li a {
    font-size: 15px;
    color: #000;
}

header h1 {
    flex: 1;
    text-align: center;
}
header h1 a {
    color: #fff;
    text-align: center;
    font-family: 'Archivo Black', sans-serif;
    text-shadow: 1px 1px 0 #000, -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000;
}
header h1:hover a {
    color: #000;
    animation: wave linear 1s 1;
}

@keyframes wave {
    0% {
        clip-path: polygon(0 100%, 26% 100%, 51% 100%, 73% 100%, 85% 100%, 100% 100%, 100% 100%, 0 100%);
    }
    50% {
        clip-path: polygon(0 63%, 17% 51%, 43% 63%, 64% 77%, 87% 62%, 100% 49%, 100% 100%, 0 100%);
    }
    100% {
        clip-path: polygon(0 0, 17% 0, 42% 0, 60% 0, 81% 0, 100% 0, 100% 100%, 0 100%);
    }
}

header button {
    flex-basis: 150px;
    height: 100%;
    font-size: 25px;
    line-height: 22.5px;
    margin-left: auto;
}

/* 바디 */
.container {
    position: relative;
    width: 100vw;
    height: 800px;
    /* text-align: center; */
}

.home-main {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    height: 100%;
    /* height: calc(100% - 80px); */
}
.main {
    display: flex;
    min-height: 800px;
    /* border: 1px solid; */
}
.list {
    width: 100%;
    padding-left: 50px;
}

.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 200px;
    height: 300px;
    text-align: center;
    border: 1px solid;
    padding: 10px 0;
}
.profile img {
    width: 150px;
    height: 150px;
    object-fit: contain;
}
.profile .sns-icons {
    display: flex;
    gap: 10px;
}
.profile .sns-icons button:hover svg {
    stroke: #3a3a3a;
    fill: #3a3a3a;
}

.title {
    text-transform: uppercase;
    font-family: 'Archivo', sans-serif;
    color: #000;
}
.title p:nth-child(1) {
    font-size: 6em;
    font-style: italic;
    font-weight: bold;
    letter-spacing: 10px;
}
.title p:nth-child(2) {
    font-size: 4em;
    margin-top: -60px;
    margin-left: 17px;
}
.sub-title button {
    position: relative;
    width: 100px;
    height: 100px;
    border: 2px solid;
    border-radius: 50%;
}
.sub-title button svg {
    width: 50px;
    height: 50px;
}

.wrap {
    width: 1000px;
    margin: 30px auto;
}

.head {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

label {
    cursor: pointer;
    margin-right: 10px;
}
.search {
    width: 200px;
    height: 20px;
    border: 1px solid;
    outline: none;
    background: #fff;
    border-radius: 10px;
    text-indent: 10px;
}

.page-item .page-link {
    color: #000;
}
.active .page-link {
    color: #fff;
    background: #000;
    border: 1px solid #000;
}

/* 토스트 */
.toast-div {
    position: fixed;
    bottom: 20px;
    right: 20px;
}
.alert-toast {
    position: relative;
    width: 200px;
    text-align: center;
    color: #fff;
    background: #000;
    border-radius: 10px;
}
.alert-toast::after {
    content: '';
    position: absolute;
    right: -9px;
    bottom: 0;
    display: inline-block;
    width: 15px;
    height: 15px;
    border: 15px solid;
    border-color: transparent transparent #000 transparent;
}

.footer {
    font-family: 'Gruppo', sans-serif;
    font-weight: bold;
    text-align: center;
}
